* {
  box-sizing: border-box;
}
body, html {
  height: 100%;
  margin: 0;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  height: 100%;
}

.loading {
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: center;
}

.lds-dual-ring {
    display: inline-block;
    align-self: center;
    width: 128px;
    height: 128px;
  }
  .lds-dual-ring:after {
    content: " ";
    display: block;
    width: 92px;
    height: 92px;
    margin: 1px;
    border-radius: 50%;
    border: 5px solid #000;
    border-color: #000 transparent #000 transparent;
    -webkit-animation: lds-dual-ring 1.2s linear infinite;
            animation: lds-dual-ring 1.2s linear infinite;
  }
  @-webkit-keyframes lds-dual-ring {
    0% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  @keyframes lds-dual-ring {
    0% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  
.footer {
    display: flex;
    justify-content: center;
    align-self: flex-end;
    grid-area: foo;
    border-radius: 5px;
    height: 40px;
    background-color: black;
    padding: 10px;
    color: white;
    text-align: center;
}

@media (max-width: 667px) {
    .footer {
        height: 60px;
    }
}
.container {
    width: 100%;
    height: 100%
}

.bg {
    height: 100%;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(3, 1fr);
    background: linear-gradient(to bottom, rgba(62, 62, 62, 0.75) 0%, rgba(62, 62, 62, 0.75) 100%), url(/expense-tracker/static/media/background.56589819.jpg) center center no-repeat;
    background-size: cover;
    grid-template-areas:
    ". . . ."
    ". button button ."
    "foo foo foo foo";
}

.bt {
    grid-area: button;
    justify-self: center;
    display: flex;
    justify-content: center;
    width: 100px;
    height: auto;
    background-color: lightblue;
    padding: 10px 20px; 
    border-radius: 5px;
    align-self: center;
    cursor: pointer;
}

.typo {
    grid-area: button;
    text-align: center;
    color: white;
}

.bt p {
    text-align: center;
    color: white;
    font-weight: bold;
    font-size: 4rem;
}

.bt:hover {
    background-color: darkblue;
    color: white;
}


@media (max-width: 667px) {
.bg {
    grid-template-columns: repeat(1, 1fr);
    grid-template-areas:
    "."
    "button"
    "foo";
    }
.typo {
    font-size: 0.7rem;
    }
}
.signin {
    display: flex;
    justify-content: center;
    width: 100%;
    height: 100%;
}

.back {
    border: 1px solid gray; 
    padding: 5px; cursor: pointer;
    width: 100px; 
    text-align: center; 
    margin: 5px; 
    border-radius: 5px; 
    background-color: wheat;
} 
.back:hover {
    background-color: rgb(206, 160, 76);
    color: white;
}

.signin .box {
    display: grid;
    justify-content: center;
    width: 400px;
    height: auto;
    align-self: center;
    border: 1px solid black;
    border-radius: 5px;
    background-color: white;
    padding: 20px;
}

.signin .box input {
    width: 150px;
    height: auto;
    border-radius: 5px;
    padding: 5px;
    border: 1px solid gray;
}
.navbar {
    display: flex;
    grid-area: navbar;
    width: 100%;
    height: 80px;
    justify-content: center;
    background: linear-gradient(to bottom, white, rgb(221, 221, 221));
    color: black;
    font-weight: bold;
    font-size: 1.5rem;
}

@media (max-width: 657px) {
    .navbar {
        width: 100%;
        height: 80px;
    }
}
.modal {
    position: fixed;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(3, 1fr);
    grid-template-areas:
    ". . ."
    "box box box"
    ". . .";
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    justify-content: center;
    z-index: 100;
}

.backdrop {
    width: 800%;
    height: 800%;
    top: 0;
    left: 0;
    background-color: rgba(128,128,128,0.4);
}

.modalBox {
    grid-area: box;
    display: flex;
    width: 50%;
    height: auto;
    background-color: white;
    justify-self: center;
    justify-content: center;
    align-content: center;
    padding: 10px;
}

@media (max-width: 657px) {
    .modalBox {
        width: 75%;
    }
}
.addexp {
    display: grid;
    justify-content: center;
    align-self: center;
}

.addexp h2 {
    justify-self: center;
}

.addexp .form {
    justify-self: center;
}

.addexp input {
    width: 200px;
    height: 40px;
    border-radius: 10px;
    padding-left: 2px;
}

.addBtn {
    justify-self: center;
    margin-top: 10px;
    width: 100px;
    padding: 5px;
    text-align: center;
    border: 1px solid black;
    border-radius: 5px;
    cursor: pointer;
}

.addBtn:hover {
    background-color: beige;
}
.expenseLog {
    display: grid;
    width: 100%;
    height: auto;
    grid-template-columns: 1fr 2fr 1fr;
    grid-template-rows: 1fr;
    grid-template-areas: 
    "date detail sp";
    padding: 5px;
    border-top: 1px solid rgb(206, 206, 206);
}


.date {
    display: flex;
    grid-area: date;
    justify-content: center;
    text-align: center;
    border-right: 0.5px solid gray;
    align-content: center;
}

.detail {
    display: flex;
    grid-area: detail;
    align-self: center;
    justify-content: center;
    border-right: 0.5px solid gray;
    align-self: center;
    height: 70px;
    text-transform: capitalize;
}

.sp {
    display: flex;
    grid-area: sp;
    align-self: center;
    justify-content: center;
}

.delete {
    grid-area: sp;
    justify-self: flex-end;
    padding: 5px;
    cursor: pointer;
}
.itemPro {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-template-areas:
    "user user"
    "profile logout";
    justify-content: center;
    align-content: center;
}

.user {
    grid-area: user;
    font-size: 2rem;
    justify-self: center;
    text-transform: capitalize;
}

.profile .profileBtn {
    align-self: flex-end;
    grid-area: profile;
    width: 150px;
    height: 40px;
    border: 1px solid black;
    border-radius: 5px;
    padding: 10px;
    margin: 5px;
    text-align: center;
    cursor: pointer;
}

.profile .profileBtn:hover {
    background-color: beige;
}

.profile .logoutBtn {
    align-self: flex-end;
    grid-area: logout;
    width: 150px;
    height: 40px;
    border: 1px solid black;
    border-radius: 5px;
    padding: 10px;
    margin: 5px;
    text-align: center;
    cursor: pointer;
}

.profile .logoutBtn:hover {
    background-color: beige;
}
.dashboard {
    display: grid;
    width: 100%;
    height: 100%;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: auto;
    grid-template-areas:
    "navbar navbar navbar" 
    "counter counter profile"
    "log log add"
    "log log graph"
    "foo foo foo";
    justify-content: center;
}

.counter {
    grid-area: counter;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: auto;
    grid-template-areas:
    "number";
    border: 1px solid gray;
    border-radius: 5px;
    width: 100%;
    height: 100%;
    padding: 5px;
    background-color: white;
    align-content: center;
}

.counter .countBt {
    display: none;
}

.profile {
    grid-area: profile;
    display: flex;
    justify-content: center;
    align-self: center;
}

.add {
    grid-area: add;
    display: flex;
    justify-content: center;
    align-self: center;
}

.graph {
    grid-area: graph;
    display: flex;
    justify-content: center;
}

.log {
    grid-area: log;
    border-right: 1px solid gray;
    margin-bottom: 7px;
    margin-top: 10px;
}

.counter .text {
    grid-area: number;
    justify-self: center;
    font-size: 4rem;
    color: black;
}

.countBtGraph {
    display: none;
}

.phoneLogout {
    display: none;
}

@media (max-width: 657px) {
    .dashboard {
        display: grid;
        width: 100%;
        height: 100%;
        grid-template-columns: 1fr;
        grid-template-rows: repeat(5, 1fr);
        grid-template-areas:
        "navbar" 
        "logout"
        "counter"
        "log"
        "foo";
        justify-content: center;
    }

    .counter {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: repeat(3, 1fr);
        grid-template-areas:
        "number number number"
        "button graph graph"
        "curren curren curren";
        grid-area: counter;
        border: 1px solid gray;
        border-radius: 5px;
        width: 100%;
        height: 100%;
        padding: 5px;
        background-color: white;
    }

    .counter .text {
        grid-area: number;
        justify-self: center;
        font-size: 4rem;
        color: black;
    }

    .counter .countBt {
        display: flex;
        grid-area: button;
        justify-self: center;
        text-align: center;
        padding: 6px;
        margin: 1px 0px;
        border-radius: 100%;
        background-color: crimson;
        color: white;
        font-weight: bold;
        cursor: pointer;
        justify-content: center;
        align-self: center;
        align-content: center;
        font-size: 2.4rem;
    }

    .counter .countBtGraph {
        display: flex;
        grid-area: graph;
        justify-self: center;
        text-align: center;
        padding: 6px;
        border-radius: 100%;
        background-color: crimson;
        color: white;
        font-weight: bold;
        cursor: pointer;
        justify-content: center;
        align-self: center;
        align-content: center;
        font-size: 2.4rem;
    }

    .changeCurrency {
        grid-area: curren;
        justify-self: center;
        align-self: center;
    }

    .profile {
        display: none;
    }

    .log {
        grid-area: log;
        margin-bottom: 0px;
        border: 0px;
    }

    .add {
        display: none
    }
    
    .graph {
        display: none;
    }

    .counter .countBt:hover {
        background-color: brown;
    }
    .phoneLogout {
        grid-area: logout;
        border: 1px solid gray; 
        padding: 5px; cursor: pointer;
        width: 80px;
        height: 60px; 
        text-align: center; 
        margin: 5px; 
        border-radius: 5px; 
        background-color: rgb(0, 0, 0);
        color: white;
    }
}
